import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import TotalStudets from "./TotalStudents";
import DashGraph from "./DashGraph";
import { useState, useEffect } from "react";
import {customAxios} from "../mangement/apis/Axios";
import { useNavigate} from "react-router-dom"
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    marginTop: 20,
    paddingLeft: 30,
    paddingRight: 30,
  },
  paper: {
    padding: theme.spacing(2),
    // textAlign: "center",
    height: "120px",
    color: theme.palette.text.secondary,
  },
}));

export default function Dash() {
  const navigate = useNavigate;
  const classes = useStyles();
  const [total, setTotal] = useState({
    studentCount: 0,
    teacherCount: 0,
    transportCount: 0,
    totalBalance: 0,
    totalPaid: 0,
  });

  const Data = async () => {
    // const data = await axios.get("http://localhost:8000/list/total");
    const Data = await customAxios.getCall("list/total");
    // console.log(data.data)
    setTotal(Data);
  };
  useEffect(() => {
    Data();
  }, []);
  return (
    <>
      <div className={classes.root}>
        <Grid container spacing={3}>
          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>
              <TotalStudets
                text={"Total Students"}
                total={total.studentCount}
              />
            </Paper>
          </Grid>
          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>
              <TotalStudets text={"Total Staff"} total={total.teacherCount} />
            </Paper>
          </Grid>

          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>
              <TotalStudets
                text={"Total Transport"}
                total={total.transportCount}
              />
            </Paper>
          </Grid>
          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>
              <TotalStudets text={"Estimate Fee"} total={`RS ${total.totalBalance}`} />
            </Paper>
          </Grid>
          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>
              <TotalStudets text={"Total Collection"} total={`RS ${total.totalPaid}`} />
            </Paper>
          </Grid>
          {/*
          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>
              {" "}
              <TotalStudets text={"Total Studets"} numberofstudent={0} />
            </Paper> */}
          {/* </Grid> */}
        </Grid>
      </div>
      <div style={{ paddingLeft: 9 }}>
        <DashGraph  />
      </div>
    </>
  );
}
